$mobile: "(max-width: 768px)";
$tablet: "(min-width: 769px) and (max-width: 1200px)";
$desktop: "(min-width: 1199px)";

@media #{$desktop} {

  .contact__box {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;

    h1 {
      font-family: 'Megrim', cursive;

      width: 100%;
      padding: 1rem;
      text-align: center;
      background-color: #F8A5BF;
      opacity: 0.5;
    }

    .contact__form {
      width: 100%;
      background-image: url("../../assets/634100-POB6T4-481-min.jpg");
      background-size: cover;
      background-position: bottom;
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;
      height: 48rem;


      form {
        margin-top: 6rem;

        ::placeholder {
          font-size: 1.3rem;
          color: #F8A5BF;
          text-align: center;
        }

        label {
          display: flex;
          align-items: center;
          font-size: 3rem;
          width: 40rem;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          justify-content: flex-start;


          .font-awesome {
            width: 3rem;
            margin-left: 2rem;
            color: grey;

          }


          input {
            width: 100%;
            height: 3rem;
            margin: 1rem;
            outline: none;
            background-image: none;
            background-color: transparent;
            -webkit-box-shadow: none;
            -moz-box-shadow: none;
            box-shadow: none;
            border: 1px solid grey;
            border-radius: 0.5rem;
            font-size: 1.5rem;

          }

          textarea {
            width: 100%;
            height: 4rem;
            margin: 1rem;
            border: 1px solid grey;
            border-radius: 0.5rem;
            background-image: none;
            background-color: transparent;
            -webkit-box-shadow: none;
            outline: none;

            -moz-box-shadow: none;
            box-shadow: none;
            font-size: 1.5rem;
          }

        }

        input[type="button"] {
          font-family: 'Megrim', cursive;
          height: 3rem;
          margin: 1rem 1rem 1rem 6rem;
          width: 20%;
          font-size: 1.5rem;
          background-image: none;
          background-color: transparent;
          -webkit-box-shadow: none;
          -moz-box-shadow: none;
          box-shadow: none;
          border: 1px solid grey;
          border-radius: 0.5rem;

        }
      }
    }
  }

}

@media #{$mobile,$tablet}{
  .contact__box {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1rem;
    border-top: 2px solid whitesmoke;
    padding-top: 1rem;


    h1 {
      font-family: 'Megrim', cursive;

      width: 100%;
      padding: 2rem;
      text-align: center;
      background-color: #F8A5BF;
