        flex-direction:column;
        width:100%;

        ::placeholder {
          font-size: 1rem;
          color: #F8A5BF;
          text-align: center;
        }

        label {
          display: flex;
          align-items: center;
          font-size: 3rem;
          width: 100%;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;


          .font-awesome {
            width: 1.5rem;
            margin-left: 2rem;
            color: grey;

          }


          input {
            width: 100%;
            height: 3rem;
            margin: 1rem;
            outline: none;
            background-image: none;
            background-color: transparent;
            -webkit-box-shadow: none;
            -moz-box-shadow: none;
            box-shadow: none;
            border: 1px solid grey;
            border-radius: 0.5rem;
            font-size: 1.5rem;

          }

          textarea {
            width: 100%;
            height: 4rem;
            margin: 1rem;
            border: 1px solid grey;
            border-radius: 0.5rem;
            background-image: none;
            background-color: transparent;
            -webkit-box-shadow: none;
            outline: none;

            -moz-box-shadow: none;
            box-shadow: none;
            font-size: 1.5rem;
          }

        }

        input[type="button"] {
          font-family: 'Megrim', cursive;
          height: 3rem;
          margin: 1rem 1rem 1rem 5rem;
          width: 30%;
          font-size: 1.5rem;
          background-image: none;
          background-color: transparent;
          -webkit-box-shadow: none;
          -moz-box-shadow: none;
          box-shadow: none;
          border: 1px solid grey;
          border-radius: 0.5rem;

        }
      }
    }
  }
}