$mobile: "(max-width: 768px)";
$tablet: "(min-width: 769px) and (max-width: 1200px)";
$desktop: "(min-width: 1199px)";

@media   #{$desktop,$tablet}{
  @-webkit-keyframes shadow-drop-2-center {
    0% {
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-transform: translateZ(50px);
      transform: translateZ(50px);
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
      box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }
  @keyframes shadow-drop-2-center {
    0% {
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-transform: translateZ(50px);
      transform: translateZ(50px);
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
      box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }

  .widgetProducts__box {
    width: 100%;
    height: 25rem;
    display: flex;
    align-items: center;
    justify-content: space-between;


    .widgetProducts__widgets {
      width: 30%;
      height: 15rem;
      background-color: #EEEAEE;
      align-items: center;
      display: flex;
      margin: 0;

      &:nth-of-type(2) {
        background-image: url("../../assets/564440-PL47KI-143.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        &:hover{
          -webkit-animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
      }}

      &:nth-of-type(3) {
        background-image: url("../../assets/miniPudełka-min.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        &:hover{
          -webkit-animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

        }
      }

      &:nth-of-type(1) {
        background-image: url("../../assets/2878411-min.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        &:hover{
          -webkit-animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
          animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

        }
      }

      span {
        width: 100%;
        height: 5rem;
        background-color: whitesmoke;
        opacity: 0.5;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}

@media  #{$mobile}{
  .widgetProducts__box {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;


    .widgetProducts__widgets {
      //border: 0.25rem solid #CECFD4;
      width: 100%;
      height: 15rem;
      background-color: grey;
      display: flex;
      align-items: center;
      margin: 1rem;
      //border-radius: 5%;


      &:nth-of-type(2) {
        background-image: url("../../assets/564440-PL47KI-143.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
      }

      &:nth-of-type(3) {
        background-image: url("../../assets/miniPudełka-min.jpg");
        background-size: cover;
        background-repeat: no-repeat;

      }

      &:nth-of-type(1) {
        background-image: url("../../assets/2878411-min.jpg");
        background-size: cover;
        background-repeat: no-repeat;
      }

      span {
        width: 100%;
        height: 10rem;
        background-color: whitesmoke;
        opacity: 0.5;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}